import React, {useState, useEffect, useContext, useRef} from "react";
import ReactTable from 'react-table';
import "react-table/react-table.css";
import styles from './style.module.css';
import cn from 'classnames';
import './reactTableStyle.css';
import deleteIconPath from './icons/delete.png';
import editIconPath from './icons/edit.png';
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../core/TouchableOpacity";
import {DataContext} from "../../../Dashboard";

const ROW_HEIGHT = 50;
// doublicated in reactTableStyle


// TODO limit letters count
const Cell = ({editable, className, deletingPlaceholder, saveData, bufferValue, colIndex, ...props}) => {
  const cellEl = useRef(null);

  const onEnter = (e) => {
    if (e.keyCode === 13) e.target.blur();
  };

  useEffect(() => {
    if (!cellEl.current) return;
    cellEl.current.addEventListener('keydown', onEnter);
    return () => cellEl.current.removeEventListener('keydown', onEnter);
  });

  return deletingPlaceholder ? deletingPlaceholder
    : (<div
        ref={cellEl}
        contentEditable={editable}
        suppressContentEditableWarning
        onClick={() => console.log(props)}
        // tabIndex={`${props.viewIndex ? props.viewIndex : ''}${colIndex}`}
        onBlur={saveData}
        className={cn(styles.cell, editable && styles.editable, className)}
      >
        {bufferValue ? bufferValue : props.value}
      </div>);
};

const ControlCell = ({setEditingItem, saveBufferToData, cancelEdit, deleteItem, editing}) => (
  <div className={cn(styles.cell, styles.controlCell)}>
    <TouchableOpacity>
      {editing ?
        <div onClick={saveBufferToData} className={styles.save}><i className={"fas fa-check"}/></div>
        :
        <Image onClick={setEditingItem} src={editIconPath} height={20} className={styles.controlIcon}/>
      }
    </TouchableOpacity>
    <TouchableOpacity>
      {editing ?
        <div onClick={cancelEdit} className={styles.cancel}><i className="fas fa-ban"/></div>
        :
        <Image onClick={deleteItem} src={deleteIconPath} height={20} className={styles.controlIcon}/>
      }
    </TouchableOpacity>
  </div>
);

const EditableTable = ({
  data,
  columns: inputColumns,
  deleteItem: deleteDataItem,
  editItem: editDataItem,
  headerClassName,
  ...otherProps
}) => {
  // layout
  // TODO page size!
  const getPageSize = () => Math.floor((document.documentElement.clientHeight - 240)/ROW_HEIGHT);
  const [pageSize, setPageSize] = useState(getPageSize());

  const onResize = () => setPageSize(getPageSize());

  useEffect(() => {
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, []);

  // editing
  // 1) set item and its data by code
  // 2) change its data with buffer while editing
  // 3) save buffer in data store (if saved)
  const [editingItem, setEditingItemState] = useState({});

  const setEditingItem = (code) => {
    const buffer = data.find(item => item.code === code);
    // console.log(data);
    setEditingItemState({code, buffer});
  };

  const addColumnToBuffer = (id, text) => {
    // TODO check [] syntax with Object.assign
    const buffer = {...editingItem.buffer};
    buffer[id] = text;
    setEditingItemState({code: editingItem.code, buffer});
  };

  const saveBufferToData = () => {
    editDataItem(editingItem.code, editingItem.buffer);
    cancelEdit();
  };

  const cancelEdit = () => setEditingItemState({});

  // deleting
  const [waits, setWaits] = useState([]);

  const addWait = (code) => {
    setWaits([...waits, code]);
  };

  const removeWait = (code) => {
    setWaits(waits.filter(item => item.code === code));
  };

  const deleteItem = (code) => {
    deleteDataItem(code);
    removeWait(code);
  };

  // TODO refactor !!!!!! just 2 healthy components!
  const columns = inputColumns
    .map(item => ({
      Cell: ({original, column, ...props}) => {
        const {code} = original;
        const {id} = column;

        // if item is waiting for delete, create placeholder to replace cells
        const deletingPlaceholder =
          id === 'code' &&
          waits.includes(code) &&
          <DeletingPlaceholder
            code={code}
            // remain={waits[waitingIndex].duration}
            undo={() => removeWait(code)}
            close={() => deleteItem(code)}
          />;

        return (
          <Cell
            editable={id !== 'code' && editingItem.code === code}
            deletingPlaceholder={deletingPlaceholder}
            saveData={(e) => addColumnToBuffer(id, e.target.innerText)}
            bufferValue={code === editingItem.code && editingItem.buffer[id]}
            colIndex={column.index}
            {...props}
          />
        );
      },
      headerClassName,
      ...item
    }))
    .concat({
      Cell: ({original}) => (
        <ControlCell
          setEditingItem={() => setEditingItem(original.code)}
          saveBufferToData={saveBufferToData}
          cancelEdit={cancelEdit}
          code={original.code}
          editing={editingItem.code === original.code}
          deleteItem={() => addWait(original.code)}
        />
      ),
      headerClassName,
      width: 80,
    });


  // horizontal scrolling
  function onWheel(e) {
    // console.log(e);
    this.scrollLeft += e.deltaY*0.33;
  }

  const containerRef = useRef();
  useEffect(() => {
    if (!containerRef) return;
    const table = containerRef.current.querySelector('.rt-table');
    table.addEventListener('wheel', onWheel);
    return () => table.removeEventListener('wheel', onWheel);
  }, [containerRef]);

  return (
    <div ref={containerRef} className={styles.container}>
    <ReactTable
      data={data}
      columns={columns}
      showPageSizeOptions={false}
      sortable={false}
      // resizable={false}

      NextComponent={Right}
      PreviousComponent={Left}
      // TODO change!
      minRows={pageSize}
      // TODO change!
      pageSize={pageSize}

      getTrProps={(state, rowInfo) => {
        if (!rowInfo) return {};

        return {
          style: {
            backgroundColor: rowInfo.original.code === editingItem.code ? '#ECF4F7' : 'transparent',
          }
        };
      }}

      defaultSorted={[
        {
          id: 'code',
          desc: true,
        }
      ]}

      {...otherProps}
    />
    </div>
  );
};

const Left = ({children , ...props}) => (
  <TouchableOpacity><i className="fas fa-chevron-left" {...props}/></TouchableOpacity>
);

const Right = ({children , ...props}) => (
  <TouchableOpacity><i className="fas fa-chevron-right" {...props}/></TouchableOpacity>
);

const DeletingPlaceholder = ({code, undo, close}) => (
  <div className={styles.placeholder}>
    This item with code <span className={styles.code}>#{code}</span> has been deleted.
    <span onClick={undo} className={styles.undo}>Undo</span>
    <TouchableOpacity onClick={close} className={styles.close}><i className={"fas fa-times"}/></TouchableOpacity>
  </div>
);

export default EditableTable;