import {StateField} from "./fields";
import {setAccountData, setBusinessData, setCompanyData} from "./dataSetters";
import {getAccountData, getBusinessData, getCompanyData} from "./dataGetters";
import {useEffect, useState} from "react";

const useApplicationData = () => {
  // let you know if ...
  const [isDataLoaded, setDataLoaded] = useState(false);

  // state fields
  const [account, setAccount] = useState(StateField.default);
  const [business, setBusiness] = useState(StateField.default);
  const [company, setCompany] = useState(StateField.default);

  const getInitialApplicationData = () => (
    Promise.all([
      getAccountData(),
      getCompanyData(),
      getBusinessData(),
    ])
  );

  const setInitialApplicationData = ([
    initAccountData,
    initCompanyData,
    initBusinessData,
  ]) => {
    setAccount(new StateField(initAccountData, getAccountData, setAccountData, setAccount));
    setCompany(new StateField(initCompanyData, getCompanyData, setCompanyData, setCompany));
    setBusiness(new StateField(initBusinessData, getBusinessData, setBusinessData, setBusiness));
  };

  useEffect(() => {
    getInitialApplicationData().then(setInitialApplicationData);
  }, []);

  useEffect(() => {
    if (
      !isDataLoaded &&
      account !== StateField.default &&
      company !== StateField.default &&
      business !== StateField.default
    ) {
      setDataLoaded(true);
    }
  }, [account, company, business]);


  return [
    isDataLoaded,
    {
      account,
      company,
      business,
    }
  ];
};

export default useApplicationData;