import ProductsTab from "../Products/ProductsTab";
import Button from "../../../common/core/Button";
import CataloguesTab from "../Products/CataloguesTab";
import React, {useContext, useState} from "react";
import AddNewProduct from "../Products/AddNewProduct";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import ElectByIndex from "../../../common/core/ElectByIndex";
import Wrapper from "../../../common/core/Wrapper";
import CustomersB2B from './CustomersB2B';
import CustomersB2P from './CustomersB2P';
import AddNewB2B from './AddNewB2B';
import AddNewB2P from './AddNewB2P';
import DataContext from "../../../../DATA_CONTROL/DataContext";

// const CustomersB2B = () => null;
// const CustomersB2P = () => null;
// const AddNewB2B = () => null;
// const AddNewB2P = () => null;


const Customers = () => {
  const [modalIndex, setModalIndex] = useState(null);
  const dataContext = useContext(DataContext);

  const addB2B = (formState) => {
    dataContext.customersB2B.add(formState);
    closeModal();
  };

  const addB2P = (formState) => {
    dataContext.customersB2P.add(formState);
    closeModal();
  };

  const closeModal = () => setModalIndex(null);


  const TABS = [{
    name: 'CUSTOMERS B2B',
    content: <CustomersB2B/>,
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setModalIndex(0)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }, {
    name: 'CUSTOMERS B2P',
    content: <CustomersB2P/>,
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setModalIndex(1)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }];

  const contents = TABS.map(item => <Wrapper>{item.content}</Wrapper>);

  const MODALS = [
    <AddNewB2B
      onSubmit={addB2B}
      close={closeModal}
    />,
    <AddNewB2P
      onSubmit={addB2P}
      close={closeModal}
    />,
  ];

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={contents}
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={modalIndex}
      defaultComponent={main}
    >
      {MODALS}
    </ElectByIndex>
  );

};

export default Customers;