import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import DataContext from "../../../../DATA_CONTROL/DataContext";

const columns = [{
  Header: 'CODE#',
  accessor: 'code',
  width: 65,
  index: 0,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 120,
  index: 1,
},{
  Header: 'PRICE',
  accessor: 'price',
  width: 80,
  index: 2,
},{
  Header: 'U.M.',
  accessor: 'measure',
  width: 80,
  index: 3,
},{
  Header: 'PRODUCER',
  accessor: 'producer',
  width: 120,
  index: 4,
},{
  Header: 'GENERAL DESCRIPTION',
  accessor: 'description',
  minWidth: 250,
  index: 5,
},{
  Header: 'DELIVERY',
  accessor: 'delivery',
  width: 85,
  index: 6,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
  index: 7,
},];

function ProductsTab () {
  const dataContext = useContext(DataContext);

  console.log(dataContext);

  return (
    <EditableTable
      data={dataContext.products.data}
      deleteItem={dataContext.products.delete}
      editItem={dataContext.products.edit}
      columns={columns}
    />
  );
}


export default ProductsTab;