import React, {useState, useContext} from "react";
import styles from './style.module.css';
import ElectById from "../../../common/core/ElectById";
import Main from './Main';
import Editor from "./Editor";
import DataContext from "../../../../DATA_CONTROL/DataContext";
import createTemplateInfo from "../../../../DATA_CONTROL/additional/createTemplateInfo";

const itemShape = {date: '20.06.20', title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'};
// TODO extract!
// DEV
const templates = [...Array(7)].fill(itemShape);
const documents = templates.slice(3);

// AUTOSAVE TIMER HERE?

function Templates() {
  const dataContext = useContext(DataContext);

  const [activeElementId, setActiveElementId] = useState('main');

  const openMain = () => setActiveElementId('main');
  const openEditor = () => setActiveElementId('editor');

  const saveAndCloseEditor = (templateData) => {
    const template = createTemplateInfo(templateData);
    dataContext.templates.add(template);
    closeEditor();
  };

  const closeEditor = () => {
    openMain();
  };

  const ELEMENTS = [{
    id: 'main',
    component: <Main
      openEditor={openEditor}
      templates={dataContext.templates.data}
      documents={documents}
    />,
  },{
    id: 'editor',
    component: <Editor
      onSave={saveAndCloseEditor}
      onCancel={closeEditor}
      defaultHtmlText={''}
    />,
  }];

  return (
    <ElectById activeElementId={activeElementId} containerClassName={styles.container}>
      {ELEMENTS}
    </ElectById>
  );
}

export default Templates;