import React from 'react';
import styles from './style.module.css';
import {Form} from 'informed';
import cn from 'classnames';
import Input from "../../../common/core/Input";
import Col from "react-bootstrap/Col";
import DashboardTabs from "../../../common/parts/DashboardTabs";
import Row from "react-bootstrap/Row";
import ButtonsPair from "../../../common/parts/ButtonsPair";

const CustomInput = ({title, ...rest}) => (
  <>
    <Col className={cn(styles.title, 'col-12 pl-2 mb-3')}>{title.toUpperCase()}</Col>
    <Col className={'col-12'}>
      <Input heigth={40} {...rest}/>
    </Col>
  </>
);

const CustomForm = ({onSubmit, close}) => (
  <Form onSubmit={onSubmit} className={styles.form}>
    <Row>
      <Col className={'col-5 pr-2'}>
        <CustomInput
          field={'name'}
          title={'name of product'}
        />
      </Col>
      <Col className={'col-2 px-2'}>
        <CustomInput
          field={'price'}
          title={'price'}
        />
      </Col>
      <Col className={'col-5 pl-2'}>
        <CustomInput
          field={'measure'}
          title={'u.m. (unit measure)'}
        />
      </Col>
    </Row>
    <Row className={'pt-2'}>
      <Col className={'col-5 pr-2'}>
        <CustomInput
          field={'producer'}
          title={'producer'}
        />
      </Col>
      <Col className={'col-5 pl-2'}>
        <CustomInput
          field={'delivery'}
          title={'delivery'}
        />
      </Col>
    </Row>
    <Row>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'description'}
          title={'general description'}
        />
      </Col>
      <Col className={'col-10 pt-2'}>
        <CustomInput
          field={'keywords'}
          title={'keywords'}
          placeholder={'Enter comma-separated keywords'}
        />
      </Col>
    </Row>
    <Row>
      <ButtonsPair onCancel={close} className={'mb-2'}/>
    </Row>
  </Form>
);

const AddNewProduct = ({
  onSubmit,
  close,
}) => {
  const TABS = [{
    name: 'ADD NEW PRODUCT',
    content: <CustomForm onSubmit={onSubmit} close={close}/>,
  }];

  return (
    <DashboardTabs
      tabs={TABS}
      readyContents={TABS.map(tab => tab.content)}
    />
  );
};

export default AddNewProduct;