import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import DataContext from "../../../../DATA_CONTROL/DataContext";
import styles from "./style.module.css";

const columns = [{
  Header: 'CODE#',
  accessor: 'code',
  width: 65,
  index: 0,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 100,
  index: 1,
},{
  Header: 'ADDRESS',
  accessor: 'address',
  width: 120,
  index: 2,
},{
  Header: 'EMAIL',
  accessor: 'email',
  width: 150,
  index: 3,
},{
  Header: 'PHONE',
  accessor: 'phone',
  width: 100,
  index: 4,
},{
  Header: 'WHAT DOES HE/SHE DO? (PROFESSION)',
  accessor: 'profession',
  minWidth: 150,
  index: 5,
},{
  Header: 'FACEBOOK PROFILE',
  accessor: 'facebook',
  width: 120,
  index: 6,
},{
  Header: 'DATE OF BIRTH',
  accessor: 'birth_date',
  width: 85,
  index: 7,
},{
  Header: 'WHAT DOES HE/SHE BOUGHT?',
  accessor: 'purchase',
  minWidth: 150,
  index: 8,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
  index: 9,
}];

function CustomersB2B () {
  const dataContext = useContext(DataContext);

  return (
    <EditableTable
      data={dataContext.customersB2P.data}
      deleteItem={dataContext.customersB2P.delete}
      editItem={dataContext.customersB2P.edit}
      columns={columns}
      headerClassName={styles.tableHeader}
    />
  );
}

export default CustomersB2B;