import Input from "../../common/core/Input";
import React from "react";
import cn from 'classnames';
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import moment from "moment";
import {Option, Scope, Select, Text} from 'informed';

export default () => (
  <Row>
    <Col className={'col-4'}>
      <Input field={'name'} placeholder={'Name'}/>
      <Input field={'surname'} placeholder={'Surname'}/>
      <Input
        field={'dateofbirth'}
        parse={(value) => moment(value).format("YYYY-MM-DDTHH:mm:ss.SSSZ")}
        format={(value) => moment(value).format("YYYY-MM-DD")}
        placeholder={'Date of birth'}
        required
        type={"date"}
        max={'3000-01-01'}/>
    </Col>
    <Col className={cn(styles.profileAddress, 'col-12')}>Address</Col>
    <Col className={'col-12'}>
      <Row>
        <Col className={'col-5 pr-1'}>
          <Row>
            <Scope scope={'address'}>
              <Col className={'col-7 pr-1'}>
                <Input field={'street'} placeholder={'Street'}/>
                <Input field={'city'} placeholder={'City'}/>
                <Input field={'country'} placeholder={'Country'}/>
              </Col>
              <Col className={'col-5 pl-1'}>
                <Input field={'building'} placeholder={'№'}/>
                <Input field={'index'} placeholder={'Index'}/>
                <Input field={'state'} placeholder={'State'}/>
              </Col>
            </Scope>
          </Row>
        </Col>
        <Col className={'col-7 pl-1'}>
          <Row>
            <Col className={'col-6 pr-1'}>
              <Input field={'workemail'} placeholder={'Work email'}/>
              <Input field={'alternativermail'} placeholder={'Alternative email'}/>
              <Select className={styles.selectInput} field={'gender'}>
                <Option value="" disabled>
                  Gender
                </Option>
                <Option value={0} label={'Male'}/>
                <Option value={1} label={'Female'}/>
              </Select>
            </Col>
            <Col className={'col-6 pl-1'}>
              <Input field={'facebook'} placeholder={'Facebook profile'}/>
              <Input field={'twitter'} placeholder={'Twitter profile'}/>
              <Input field={'linkedin'} placeholder={'Linked-in profile'}/>
            </Col>
          </Row>
        </Col>
      </Row>
    </Col>
  </Row>
);