import React, {useRef, useState, useEffect} from "react";
import ReactQuill from 'react-quill';
import './quill.snow.css';
import styles from './style.module.css';
import './quillStyles.css';
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Button from "../../../../common/core/Button";
import Col from "react-bootstrap/Col";
import {Text, Form} from 'informed';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import cn from 'classnames';

const modules = {
  toolbar: [
    [{ 'header': [1, 2, false] }],
    ['bold', 'italic', 'underline'],
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'align': []}],
    [{container: '#'}]
  ],
};

const Editor = ({onSave, onCancel, defaultHtmlText}) => {
  const editor = useRef();
  const [text, setText] = useState(defaultHtmlText);
  const [formApi, setFormApi] = useState(null);

  const onDragOver = (e) => {
    e.dataTransfer.dropEffect = 'copy';
    e.dataTransfer.effectAllowed = 'all';
  };

  useEffect(() => {
    if (!editor) return;
    const controller = editor.current;
    const area = controller.editor.root;
    area.addEventListener('dragover', onDragOver);

    return () => {
      area.removeEventListener('dragover', onDragOver);
    }
  });

  const save = ({title, keywords}) => {
    onSave({title, keywords, text});
    formApi.reset();
    setText(defaultHtmlText);
  };

  const cancel = () => {
    onCancel();
    setText(defaultHtmlText);
  };

  return (
    <>
      <div className={styles.main}>
        <div className={styles.mainContent}>
          <CustomForm getApi={setFormApi} onSubmit={save}/>
          <div className={styles.editor}>
            <Buttons save={formApi && formApi.submitForm} cancel={cancel}/>
            <ReactQuill
              className={'tmpl'}
              value={text}
              onChange={(text) => setText(text)}
              modules={modules}
              ref={editor}
            />
          </div>
        </div>
      </div>
      <div className={styles.fields}>
        <div className={styles.fieldsContent}>
          <div className={styles.header}>
            ADD VARIABLE FIELDS:
          </div>
          {fields.map(({text, hint}) => <Field text={text} hint={hint}/>)}
        </div>
      </div>
    </>
  );
};

const CustomForm = ({onSubmit, ...props}) => {
  const validate = value => !value || undefined;
  const [inputFails, setInputFails] = useState({});

  const submit = (formState) => {
    // TODO async so what?!!
    setFails({});
    onSubmit(formState);
  };

  const setFails = (states) => {
    setInputFails({...states});
  };

  return (
    <Container className={styles.formContainer}>
      <Form onSubmitFailure={setFails} onSubmit={submit} autoComplete="off" {...props}>
        <Row>
          <Col className={cn(styles.inputContainer, 'pr-2', inputFails.title && styles.error)} md={4}>
            <Col md={12} className={styles.title}>TITLE</Col>
            <Text
              // placeholder={'Enter title for new template'}
              field={'title'}
              validate={validate}
            />
          </Col>
          <Col className={cn(styles.inputContainer, 'pl-2', inputFails.keywords && styles.error)} md={8}>
            <Col md={12} className={styles.title}>KEYWORDS</Col>
            <Text
              placeholder={'Enter comma-separated keywords'}
              field={'keywords'}
              validate={validate}
            />
          </Col>
        </Row>
      </Form>
    </Container>
  );
};

const fields = [{
  text: 'receiving party',
  hint: 'company name',
},{
  text: 'client address',
},{
  text: 'contact person',
  hint: 'in attention to'
},{
  text: 'product/service name',
},{
  text: 'the problem',
},{
  text: 'solution',
},{
  text: 'amount',
},{
  text: 'key benefits',
},{
  text: 'amount',
},{
  text: 'your company skills',
}];

const Buttons = ({save, cancel}) => (
  <div className={styles.buttonsContainer}>
    <Button
      // thin
      title={'cancel'}
      onClick={cancel}
      fontSize={13}
      height={24}
      width={70}
      color={'#C9CBCA'}
      containerClassName={'mr-2'}
    />
    <Button
      // thin
      title={'save'}
      fontSize={13}
      height={24}
      width={70}
      onClick={save}
      containerClassName={'mr-2'}
    />
  </div>
);

const Field = ({text, hint}) => {
  const field = useRef();

  const onDragStart = (e) => {
    window.getSelection().removeAllRanges();
    const text =  e.target.querySelector(`.${styles.text}`).textContent.toUpperCase();
    e.dataTransfer.setData('text/plain', text.split(' ').join('_'));
  };

  useEffect(() => {
    if (!field) return;
    const el = field.current;

    el.addEventListener('dragstart', onDragStart);

    return () => {
      el.removeEventListener('dragstart', onDragStart);
    }
  });

  return (
    <TouchableOpacity draggable ref={field} className={styles.field}>
      <div className={styles.text}>[{text}]</div>
      {hint && <div className={styles.hint}>({hint})</div>}
    </TouchableOpacity>
  );
};


export default Editor;