import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
// import "react-table/react-table.css";
import DataContext from "../../../../DATA_CONTROL/DataContext";
import styles from './style.module.css';

const columns = [{
  Header: 'CODE#',
  accessor: 'code',
  width: 65,
  index: 0,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 100,
  index: 1,
},{
  Header: 'ADDRESS',
  accessor: 'address',
  width: 120,
  index: 2,
},{
  Header: 'EMAIL',
  accessor: 'email',
  width: 150,
  index: 3,
},{
  Header: 'PHONE',
  accessor: 'phone',
  width: 100,
  index: 4,
},{
  Header: 'WHAT DOES THEIR COMPANY DO?',
  accessor: 'specialization',
  minWidth: 140,
  index: 5,
},{
  Header: 'WHAT DOES THEIR COMPANY NEED?',
  accessor: 'needs',
  minWidth: 140,
  index: 6,
},{
  Header: 'PERSON WHO DECIDE',
  accessor: 'arbiter',
  width: 110,
  index: 7,
},{
  Header: 'WHAT DID THEY TOOK LAST TIME?',
  accessor: 'last_deal',
  minWidth: 140,
  index: 8,
},{
  Header: 'KEYWORDS',
  accessor: 'keywords',
  width: 100,
  index: 9,
}];

function CustomersB2B () {
  const dataContext = useContext(DataContext);

  return (
    <EditableTable
      data={dataContext.customersB2B.data}
      deleteItem={dataContext.customersB2B.delete}
      editItem={dataContext.customersB2B.edit}
      columns={columns}
      headerClassName={styles.tableHeader}
    />
  );
}

export default CustomersB2B;