import React, {useEffect, useRef, useState} from "react";
import Wrapper from "../../common/core/Wrapper";
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import {Col12} from "../../common/core/CustomBootstrap/cols";
//dev
import avatarImagePath from '../images/robot.jpg';
import Image from "react-bootstrap/Image";
import cn from 'classnames';
import Select from 'react-select';
import Button from "../../common/core/Button";
import microphoneIconPath from '../images/icons/microphone.png';
import {ScaleLoader, BounceLoader} from 'react-spinners';
import useRecorder from "./useRecorder";
import getGoogleSpeechTranscript from "./actions/getGoogleSpeechTranscript";
import { css } from '@emotion/core';
import getGoogleTextToSpeech from "./actions/getGoogleTextToSpeech";
import useEscape from "./useEscape";
import StopButton from "./actions/StopButton";

const AVATAR_WIDTH = 180;
const AVATAR_UP_OFFSET = 60;
const AVATAR_CONTAINER_SIDE = 110;

// TODO ?
function Option(value, label) {
  this.value = value;
  this.label = label;
}

// const templates = [
//   new Option('standard', 'Standard template'),
// ];

const STEPS = [{
  question: 'Which template do you want to use?',
  fakeAnswer: 'Standard template',
},{
  question: 'What is the name of the receiving party?',
},{
  question: 'What is the client address?',
},{
  question: 'What is the problem?',
},{
  question: 'What is your solution?',
},{
  question: 'Your document was created successfully. Have a nice day!',
}];

// const initMessage = 'Which template do you want to use?';
const RECORD_TIME = 3;

const Assistant = ({close}) => {
  //dev
  const [step, setStepState] = useState(0);
  const nextStep = () => {
    if (step === STEPS.length-1) return;
    setStepState(s => s+1);
  };
  const stepIndex = useRef();
  useEffect(() => {
    setMessage(STEPS[step].question);
    stepIndex.current = step;
    setInput({});
  }, [step]);

  const [input, setInput] = useState({});

  const [thinking, setThinking] = useState(false);

  const [message, setMessageState] = useState(null);
  // useEffect(() => {
  //   setMessage(initMessage);
  // }, []);

  const setMessage = async (message) => {
    const base64data = await getGoogleTextToSpeech(message);
    const sound = new Audio("data:audio/MP3;base64," + base64data);
    sound.play();
    setMessageState(message);
  };

  //dev
  const getMessage = (transcript, step) => {
    if (step === 0) {
      return 'I found this by keywords: ' + transcript.split(' ').join(', ');
    }
    else return '';
  };

  const onRecordEnd = async (base64data) => {
    console.log(stepIndex.current);
    setRecording(false);
    setThinking(true);
    const transcript = await getGoogleSpeechTranscript(base64data);

    const message = transcript ?
      getMessage(transcript, stepIndex.current)
      : 'I heard nothing.';

    await setMessage(message);

    //dev
    if (!transcript) {
      setThinking(false);
      return;
    }

    if (stepIndex.current === 0) {
      setInput({value: STEPS[0].fakeAnswer, label: STEPS[0].fakeAnswer});
    } else {
      setInput({value: transcript, label: transcript});
    }

    setThinking(false);
  };

  const [recordDisabled, setRecordDisabled] = useState(false);
  const onMicrophoneAccessGranted = () => {
    setRecordDisabled(false);
  };
  const onMicrophoneAccessDenied = () => {
    setRecordDisabled(true);
  };








  // DEEEEEEEEEEEEV!

  // const [recStart, recForceStop] = useRecorder(onRecordEnd, onMicrophoneAccessGranted, onMicrophoneAccessDenied);



  const [recorder, setRecorder] = useState(null);

  const handleSuccess = (stream) => {
    const context = new AudioContext();
    const source = context.createMediaStreamSource(stream);
    const recorder = new window.Recorder(source, {
      callback: onRecordEndDEV,
      numChannels: 1
    });
    setRecorder(recorder);
  };

  const onRecordEndDEV = (blob) => {
    const reader = new FileReader();
    reader.readAsDataURL(blob);
    reader.onloadend = () => {
      const base64data = reader.result;
      onRecordEnd(base64data.split(',')[1]);
    };
  };

  // TODO what if two micros

  useEffect(() => {
    let savedStream = null;

    const requestMicrophoneAccess = () => {
      if (!navigator.mediaDevices) {
        onMicrophoneAccessDenied();
        return;
      }

      navigator.mediaDevices.getUserMedia({ audio: true, video: false })
        .then((stream) => {
          savedStream = stream;
          handleSuccess(stream);
          onMicrophoneAccessGranted();
        })
        .catch((err) => {
          console.log(err);
          onMicrophoneAccessDenied();
        });
    };

    const terminate = () => {
      savedStream && savedStream.getTracks()[0].stop();
      if (navigator.mediaDevices) navigator.mediaDevices.ondevicechange = null;
    };

    if (navigator.mediaDevices) navigator.mediaDevices.ondevicechange = requestMicrophoneAccess;

    requestMicrophoneAccess();

    return () => terminate();

  }, []);

  const timer = useRef();
  const recStart = (seconds) => {
    recorder.record();
    timer.current = setTimeout(() => recForceStop(), seconds*1000);
    console.log('rec start');
  };

  const recForceStop = () => {
    clearTimeout(timer.current);
    recorder.stop();
    recorder.exportWAV();
    recorder.clear();
    console.log('rec end');
  };

























  const [recording, setRecording] = useState(false);
  useEffect(() => {
    //dev
    if (recording) recStart(RECORD_TIME, step);
  }, [recording]);

  const startRecord = () => {
    setMessageState('');
    setRecording(true);
  };

  const stopRecord = () => {
    setRecording(false);
    recForceStop(step);
  };

  const containerRef = useEscape(styles.container, close);

  return (
    <Wrapper ref={containerRef} className={styles.container}>
      <Row className={styles.box}>
        <Avatar imagePath={avatarImagePath} thinking={thinking}/>
        <Dialog listening={recording} question={message}/>
        <Input
          listening={recording}
          template={input}
          setTemplate={setInput}
          recordDisabled={recordDisabled}
          onMicrophoneClick={recording ? stopRecord : startRecord}
          onSubmit={nextStep}
        />
      </Row>
    </Wrapper>
  );
};

const Dialog = ({listening, question}) => (
  <Col12 className={styles.row}>
    {listening ?
      <ScaleLoader
        height={20}
        width={4}
        color={'#ffffff'}
      />
      :
      <span className={styles.question}>{question}</span>
    }
  </Col12>
);

const BUTTON_SIZE = 55;
const INPUT_HEIGHT = 45;

const selectStyles = {
  control: (provided) => ({
    ...provided,
    height: INPUT_HEIGHT,
    borderRadius: INPUT_HEIGHT/2,
    paddingLeft: 10,
  }),
};

const Input = ({template, setTemplate, listening, onMicrophoneClick, recordDisabled, onSubmit}) => (
  <Col12 className={styles.row}>
    <div className={styles.inputContainer}>
      <div className={styles.selectContainer}>
        <Select
          className={styles.select}
          styles={selectStyles}
          value={template}
          onChange={setTemplate}
          // options={templates}
          theme={theme => ({
            ...theme,
            colors: {
              ...theme.colors,
              primary: '#1596C0',
            }
          })}
        />
        <div
          className={styles.submitUnderlier}
          style={{
            width: BUTTON_SIZE,
            height: BUTTON_SIZE,
            right: -5,
            top: -BUTTON_SIZE/2+INPUT_HEIGHT/2
          }}
        />
        <Button
          containerClassName={styles.submit}
          width={BUTTON_SIZE}
          height={BUTTON_SIZE}
          color={'#14C05E'}
          onClick={onSubmit}
          underlyingColor={'#554590'}
          containerStyle={{right: -5, top: -BUTTON_SIZE/2+INPUT_HEIGHT/2}}
          title={<i className={cn(styles.icon, 'fas fa-check')}/>}
        />
      </div>
      <Button
        containerClassName={styles.microphone}
        onClick={recordDisabled ? ()=>{} : onMicrophoneClick}
        width={BUTTON_SIZE}
        height={BUTTON_SIZE}
        className={recordDisabled ? styles.disabled : styles.enabled}
        title={listening ?
          <StopButton radius={BUTTON_SIZE/2} className={styles.timer} seconds={RECORD_TIME}/>
          :
          <Image src={microphoneIconPath}/>}
      />
    </div>
  </Col12>
);

const BLINK_INTERVAL = 400;

const Avatar = ({imagePath, thinking}) => {
  const [lighting, setLighting] = useState(false);
  const intervalRef = useRef();

  useEffect(() => {
    const startBlinking = () => {
      intervalRef.current = setInterval(() => {
        setLighting(lighting => !lighting);
      }, BLINK_INTERVAL);
    };

    const stopBlinking = () => {
      clearInterval(intervalRef.current);
      setLighting(false);
    };

    if (thinking) startBlinking();
    else stopBlinking();

    return () => stopBlinking();
  }, [thinking]);
  // TODO refactor
  useEffect(() => {
    if (!thinking && lighting) setLighting(false);
  }, [thinking, lighting]);


  // console.log(lighting);

  return (
    <Col12 className={cn(styles.row, styles.avatarRow)}>
      <div
        className={cn(styles.avatarContainer, lighting && styles.lighting)}
        style={{
          width: AVATAR_CONTAINER_SIDE,
          height: AVATAR_CONTAINER_SIDE,
          transitionDuration: `${BLINK_INTERVAL}ms`
        }}
      >
        <Image
          src={imagePath}
          width={AVATAR_WIDTH}
          className={styles.avatar}
          style={{
            marginTop: -AVATAR_UP_OFFSET,
            marginLeft: -AVATAR_WIDTH/2 + AVATAR_CONTAINER_SIDE/2,
          }}
        />
      </div>
    </Col12>
  );
};

export default Assistant;