import moment from "moment";
import getSortedByDate from "./additional/getSortedByDate";
import {GET} from "../utils/network";

const getData = async (path) => {
  try {
    const res = await GET(path);
    // console.log(res);
    return res.body.data;
  } catch (e) {
    // TODO
    // console.error(e);
    return null;
  }
};

export const getAccountData = async () => {
  return await getData('/api/Account/getinfo');
};

export const getCompanyData = async () => {
  const company = (await getData('/api/Company/getlist'))[0];
  const address = await getData(`/api/Address/get/${company.addressid}`);
  return {...company, address: {id: company.addressid, ...address}};
};

export const getBusinessData = async () => {
  return (await getData('/api/Business/getlist'))[0];
};

