import React, {useState} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import TouchableOpacity from "../TouchableOpacity";
import {PulseLoader} from "react-spinners";

const Button = ({title, waiting, onClick=()=>{}, className, containerClassName, height, width, color, thin, fontSize='inherit', containerStyle, underlyingColor='transparent', type, ...rest}) => {
  title = typeof title === 'string' ? title.toUpperCase() : title;

  return (
    <TouchableOpacity
      className={cn(styles.container, containerClassName)}
      underlyingColor={underlyingColor}
      style={{height, width, ...containerStyle}}
    >
      <button
        className={cn(styles.button, className)}
        onClick={onClick}
        style={{
          backgroundColor: color,
          fontWeight: thin ? '400' : '700',
          borderRadius: height/2,
          fontSize,
        }}
        type={type}
        {...rest}
      >
        {waiting ? <PulseLoader size={7} color={'white'}/> : title}
      </button>
    </TouchableOpacity>
  );
};

export default Button;