import {useState, useEffect} from 'react';
import {contextShape} from "./TabContext";

const START_INDEX = 0;

export default function useTabContextValue() {
  const [tabContextValue, setTabContextValue] = useState(contextShape);

  useEffect(() => {
    const value = {
      tabIndex: START_INDEX,
      setTabIndex: (index) => {
        setTabContextValue(({tabIndex, ...rest}) => ({
          tabIndex: index, ...rest
        }));
      },
    };

    setTabContextValue(value);
  }, []);

  return tabContextValue;
}