import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import styles from '../style.module.css';
import cn from 'classnames';
import {TextArea, Form} from "informed";

// passing bool 'grey' to easy style it in UserSettings....
export const Area = ({grey, className, ...props}) => (
  <Col>
    <Col className={styles.areaHeader}>{props.title}</Col>
    <TextArea required className={cn(styles.areaBusiness, grey && styles.grey, className)} {...props}/>
  </Col>
);

// passing bool 'noHeader' to easy style it in UserSettings....
export default ({noHeader, grey}) => (
  <Row>
    {!noHeader && <Row className={styles.subheader + ' mb-3'}>
      Business
    </Row>}

    <Row>
      <Col className={"col-6 m-0 pr-2 text-center"}>
        <Area field={'businessmodel'} grey={grey} title={"Business model"} placeholder={'description of business model ex: production business, retail business service supply business'}/>
        <Area field={'yearaverage'} grey={grey} title={"Year average or final production capacity"} placeholder={'trasportation methods and conditions'}/>
      </Col>
      <Col className={"col-6 m-0 pl-2 text-center"}>
        <Area field={'experience'} grey={grey} title={"Experience"} placeholder={'years, locations, domains of experience'}/>
        <Area field={'deliverymodel'} grey={grey} title={"Delivery model"} placeholder={'geographical locations and possibility to deliver the goods'}/>
      </Col>
    </Row>

    <Row>
      <Area field={'businessmission'} className={styles.lastArea} grey={grey} title={"Business mission and vision"} placeholder={'2 propositions'}/>
    </Row>

  </Row>
)