import {POST} from "../utils/network";

const setData = async (path, data) => {
  try {
    const res = await POST(path, data);
    // console.log(path, res);
  } catch (e) {
    // TODO
    console.error(path, e);
  }
};

export const setAccountData = async (data) => {
  await setData('/api/Account/update', data);
};

export const setCompanyData = async (data) => {
  await setData(`/api/Company/update`, data);
  await setData(`/api/Address/update`, data.address);
};

export const setBusinessData = async (data) => {
  await setData(`/api/Business/update`, data);
};