import Button from '../../core/Button';
import React from "react";
import styles from './style.module.css';
import cn from 'classnames';

const ButtonsPair = ({onSave=()=>{}, onCancel=()=>{}, className=''}) => (
  <div className={cn(styles.buttonsContainer, className)}>
    <Button title={'save'} type={'submit'} width={120} className={'mr-3'} onClick={onSave}/>
    <Button title={'cancel'} type={'button'} color={'#C9CBCA'} width={120} onClick={onCancel} className={'ml-3'}/>
  </div>
);

export default ButtonsPair;

