import React, {useState, useContext} from "react";
import Wrapper from "../../../common/core/Wrapper";
import DashboardTabs from '../../../common/parts/DashboardTabs';
import Button from '../../../common/core/Button';
import ServicesTab from "./ServicesTab";
import CataloguesTab from './CataloguesTab';
import AddNewService from './AddNewService';
import ElectByIndex from "../../../common/core/ElectByIndex";
import DataContext from "../../../../DATA_CONTROL/DataContext";

function Products() {
  // data!
  const dataContext = useContext(DataContext);

  const [interrupterIndex, setInterrupterIndex] = useState(null);

  const addService = (formState) => {
    dataContext.services.add(formState);
    closeInterrupter();
  };

  const closeInterrupter = () => setInterrupterIndex(null);

  const interrupters = [
    <AddNewService
      onSubmit={addService}
      close={closeInterrupter}
    />,
  ];

  // TODO refactor (mixed low and high level)
  const TABS = [{
    name: 'SERVICES',
    content: <ServicesTab/>,
    controllers: [
      <Button
        title={'add new'}
        onClick={()=>setInterrupterIndex(0)}
        height={27}
        width={100}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'import .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
      <Button
        title={'export .csv'}
        height={27}
        width={120}
        fontSize={'0.9rem'}
      />,
    ],
  }, {
    name: 'CATALOGUES',
    content: <CataloguesTab/>,
    controllers: [
      <Button
        title={'upload .pdf'}
        height={27}
        width={135}
        fontSize={'0.9rem'}
      />
    ],
  }];

  const contents = TABS.map(item => (
    <Wrapper>{item.content}</Wrapper>
  ));

  const main = <DashboardTabs
    tabs={TABS}
    readyContents={contents}
    readyControllers
    startTabIndex={0}
  />;

  return (
    <ElectByIndex
      index={interrupterIndex}
      defaultComponent={main}
    >
      {interrupters}
    </ElectByIndex>
  );
}


export default Products;